@import url(https://fonts.googleapis.com/css2?family=Abel&family=Comfortaa:wght@300&family=Montserrat&family=Orbitron&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    text-align: center;
    
}
.big_header_poster {
    width: 100vw;
    height: 100vh;
    background-image: url(/static/media/back_img.590d986d.jpg);
    background-size: cover;
    -webkit-animation-name: backswitch;
            animation-name: backswitch;
    -webkit-animation-duration: 10s;
            animation-duration: 10s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    position: fixed;
}
@-webkit-keyframes backswitch {
    0% {
        background-image: url(/static/media/back_img.590d986d.jpg);
    }
    50% {
        background-image: url(/static/media/img_2.ad45dd36.jpg);
    }
    100% {
        background-image: url(/static/media/back_img.590d986d.jpg);
    }
}
@keyframes backswitch {
    0% {
        background-image: url(/static/media/back_img.590d986d.jpg);
    }
    50% {
        background-image: url(/static/media/img_2.ad45dd36.jpg);
    }
    100% {
        background-image: url(/static/media/back_img.590d986d.jpg);
    }
}
.big_header_poster_insider {
    position: relative;
    width: 100vw;
    height: 100vh;
    
   /* background-image: linear-gradient(to right ,rgba(0, 0, 0 , 1)  , rgba(0 , 0 , 0, 0.1) );
    */z-index: 0;
}
.big_header_poster_insider_2 {
    position: relative;
    width: 100vw;
    height: 100vh;
    background-image: linear-gradient(rgba(4, 4, 179 , 0.5) ,rgba(4, 4, 179 , 0.0)  , rgba(4, 4, 179 , 0.5) );
    z-index: 0;
}
.big_header_poster_logo_outer {
    position: absolute;
    height: 80px;
    width: 80px;
    margin: 5px;
    border-radius: 50%;
    /*border: 4px solid rgba(255 , 255 ,255  , 1) ;*/
    /*background-color: rgb(66, 66, 255);*/
}
.big_header_poster_logo {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    margin-top: 60px;
    margin-left: 60px;
}
.big_header_poster_top_header {
    height: 40px;
    width: 100vw;
    background-color: None;
    box-shadow: 1px 1px 1px blue;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
}
.card {
    z-index: -1;
    position: relative;
    width: 100vw;
    height: auto;
    border-radius: 20px;
    margin: 0 auto;
    padding: 0 auto;
    padding-top: 8%;
    background-color: rgba(0, 0, 0, 0.01);
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
    /*left : 60% ;*/
    text-align: center;
}
.bigheadertext {
    color: rgba(255, 255, 255, 1);
    text-align: center;
}
.big_header_poster_top_header_options {
    min-height: 30px;
    min-width: 100vw;
    padding-top: 10px;
    padding-bottom: 14px;
    width: auto;
    position: fixed;
    right: 0px;
    text-align: center;
    display: flex;
    background-color: rgba(255, 255, 255 , 0.0);
    border-bottom: 1px solid rgba(138, 43, 226 , 0.0);
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
    transition: all ease 4s;
    
}
.displaynone{
    display: none;
    transition: all ease 2s;
}
.big_header_poster_top_header_options_after_scroll {
    min-width: 100vw;
    padding-top: 10px;
    width: auto;
    position: fixed;
    right: 0px;
    text-align: center;
    display: flex;
    border-bottom: 1px solid blueviolet;
    background-color: rgba(255, 255, 255, 1);
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
}
.lan_btn {
    z-index: 999;
    width: 90px;
    font-family: 'Abel', sans-serif;
    font-family: 'Orbitron', sans-serif;
    padding-right:50px ;
    padding-left: 5px ;
    background-color: None;
    position: fixed;
    right: 10px;
    
}
.lan_btn > select {
    z-index: 999;
    border-radius: 5px;
    border: 2px solid blueviolet;
    background-color: rgb(132, 0, 255);
    font-size: 14px;
    color :  white;
    padding: 5px;
    box-shadow: rgba(44, 44, 44, 0.25) 0px 50px 100px -20px, rgba(61, 61, 61, 0.3) 0px 30px 60px -30px;
    margin: 0 auto;
    padding: 0 auto;
    -webkit-text-decoration: None;
            text-decoration: None;
    font-family: 'Comfortaa', cursive;
    cursor: pointer;
    font-weight: bolder;
}
.lan_btn > select > option {
    background-color: gray;
    cursor: pointer;
    font-family: 'Comfortaa', cursive;
    font-weight: bolder;
}
.logo_text_after_scroll {
    z-index: 999;
    width: auto;
    font-family: 'Comfortaa' , cursive;
    padding-right:50px ;
    padding-left: 5px ;
    margin-top: 10px;
    background-color: None;
    position: fixed;
    left: 30px;
    font-weight: bold;
    color : #8a2be2 ;
}
.logo_text_before_scroll {
    z-index: 999;
    width: auto;
    font-family: 'Comfortaa' , cursive;
    padding-right:50px ;
    padding-left: 5px ;
    margin-top: 10px;
    background-color: None;
    position: fixed;
    left: 30px;
    font-weight: bold;
    color : #6f627c ;
    transition: all ease-in-out 2s;
}
.explore_btn {
    max-width: auto;
    font-family: 'Abel', sans-serif;
    /*font-family: 'Orbitron', sans-serif;*/
    padding-right:20px ;
    padding-top: 1px ;
    padding-left: 5px ;
    margin: 0 auto;
    padding: 0 auto;
    margin-left: 10px ;
    padding-left: 20px;
    transition: all ease 2s;
}
.explore_btn > h4 {
    width: 280px;
    border-radius: 8px;
   /* border: 2px solid white;*/
    background-color: rgb(106, 0, 255);
    font-size: 16px;
    color : white ; 
    font-family: 'Comfortaa', cursive;
    padding: 12px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
    margin: 0 auto;
    padding: 0 auto;
    cursor: pointer;
    margin-top: 5px;
    transition: all ease-in-out 0.4s;
}
.explore_btn > h4:hover {
    width: 240px;
    border-radius: 8px;
   /* border: 2px solid white;*/
    background-color: rgb(106, 0, 255);
    font-size: 17px;
    color : white ; 
    font-family: 'Comfortaa', cursive;
    padding: 13px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
    margin: 0 auto;
    padding: 0 auto;
    cursor: pointer;
}
.bigheadertext > h2 {
    font-size: 1.5vw;
    width: 50%;
    font-weight: bold;
    font-family: 'Abel', sans-serif;
    font-family: 'Comfortaa', cursive;
    padding: 0 auto;
    margin: 0 auto;
    margin-bottom: 15px;
    
}
.bigheadertext > h3 {
    
    font-size: 1.5vw;
    width: 60%;
    padding: 0 auto;
    margin: 0 auto;
    
    margin-bottom: 20px;
    font-family: 'Comfortaa', cursive;
    font-weight: 300 ;
}
.bigheadertext > h1 {
    position: relative;
    font-size: 2vw;
    width: 60%;
    padding: 0 auto;
    margin: 0 auto;
    font-weight:400 ;
    margin-bottom: 20px;
    font-family: 'Comfortaa', cursive;
}
.searchbar {
    display: flex;
    position: relative;
    padding: 0 auto;
    margin: 0 auto;
    margin-top: 5px;
    border-radius: 15px;
    border: 1px solid blueviolet;
    width: 30%;
    background-color: white;
    height: 30px;
}
.search {
    -webkit-text-decoration: None;
            text-decoration: None;
    outline: none;
    text-align: left; 
    background-color: none;
    background: none;
    border: none;
    color: white;
    font-family: 'Verdana', sans-serif;
    padding-left: 20px;
    font-weight: bold;
    width: 25vw;
}
.searchicon > img {
    -webkit-text-decoration: None;
            text-decoration: None;
    outline: none;
    text-align: left; 
    background-color: none;
    background: none;
    border: none;
    color: white;
    font-family: 'Verdana', sans-serif;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 8px;
}
.searchicon  {
    background-color: blueviolet;
    border-top-left-radius: 12px;
    border-bottom-left-radius: 12px;
    border-left: 1px solid white ;
    border-top: 1px solid white ;
    border-bottom: 1px solid white ;
}








.footer_area {
    position: fixed;
    height: 60px;
    width: 100vw;
    bottom: 0px;
    text-align: center;
    margin: 0 auto;
    padding: 0 auto;
    z-index: 9999;
}
.footer_area > h4 {
    color : rgba(255 , 225 ,255 , 1) ; 
    font-family: 'Comfortaa', cursive;
    position: relative;
    margin: 0 auto;
    padding: 0 auto;
    bottom: 30px;
}
.footer_space {
    height: 40px;
    width: 100vw;
    bottom: 0px;
    display: flex;
    margin: 0 auto;
    padding: 0 auto;
    overflow: scroll;
}
.bottom_box {
    height: 60px;
    width: 100vw;
    background-color: none;
    position: fixed;
    bottom: 0px;
    display: flex;
    background-image: linear-gradient(to top , rgba(0 , 0 , 0 , 0.9) , rgba(0 , 0 , 0 , 0.1));
    margin: 0 auto;
    padding: 0 auto;
    overflow: scroll;
}
*::-webkit-scrollbar{
    display: none;
}
.sponsor_place {
    width: auto;
    height: 100%;
    display: flex;
    margin: 0 auto;
    padding: 0 auto;
    cursor: pointer;
}
.sponsor_place > h4{
    display: flex;
    color : rgba(255 , 225 ,255 , 1) ; 
    font-family: 'Comfortaa', cursive;
    font-size: 15px;
    font-weight: 600;
}
.sponsor_logo {
    width: 30px;
    height: 30px;
    margin-right: 10px;
    padding-top: 12px;
    border-radius: 50%;
}



















.container {
    position: absolute;
    height: calc(100vh - 180px);
    width: 100vw;
    z-index: 9999;
    background-color: white;
    margin-top: 100vh;
    border-top: 3px solid blueviolet;
    background-position: center;
    background-size: cover;
}
.container_insider {
    height: 100%;
    width: 100%;
    background-image : linear-gradient(to top ,rgba(0 , 0 , 0 , 1) , rgba(0 , 0 , 0 , 0.1));
}

.up_icon {
    width : 50px ; 
    height : 50px ; 
}













